import React from 'react';
import './ToDo.css';

// Format single task for rendering
function ToDo({ id, toDo, isDone, handleCheck, deleteTask }) {
  // Handle CheckBox
  const checkHandle = () => {
    handleCheck(id, isDone);
  };
  // Handle delete button
  const deleteHandler = () => {
    deleteTask(id);
  };

  return (
    <section className='ToDo'>
      <div className='id'>{id}. </div>
      <div className='task'> &#8594; {toDo} </div>
      <input
        className='checkBox'
        type='checkbox'
        name='isDone'
        defaultChecked={isDone}
        onClick={checkHandle}
      ></input>
      <button className='deleteBtn btn' onClick={deleteHandler}>
        Delete
      </button>
    </section>
  );
}

export default ToDo;
