import React from 'react';
import { useState } from 'react';
import './Adding.css';

// Add new task on list
function Adding({ addTask }) {
  const [newTask, setNewTask] = useState('');

  const appendHandle = () => {
    addTask(newTask);
    setNewTask('');
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      addTask(newTask);
      setNewTask('');
    }
  };

  return (
    <div className='adding'>
      <input
        className='textbox'
        type='text'
        value={newTask}
        placeholder='Enter new task'
        onChange={(e) => {
          setNewTask(e.target.value);
        }}
        onKeyDown={handleKeyDown}
      ></input>
      <button className='appendBtn btn' name='append' onClick={appendHandle}>
        Enter
      </button>
    </div>
  );
}

export default Adding;
