import React from 'react';
import { useState } from 'react';
import './ToDoList.css';
import ToDo from './ToDo';
import RefreshClear from './RefreshClear';
import Adding from './Adding';

// Create a list of task
function ToDoList() {
  const [task, setTask] = useState({ id: 0, toDo: 'Empty', isDone: false });

  const [taskList, setTaskList] = useState([]);

  // Update new task to the list
  const addTask = (txt) => {
    if (txt !== '') {
      txt = txt.charAt(0).toUpperCase() + txt.slice(1);
      let newId = task.id + 1;
      const newTask = { ...task, id: newId, toDo: txt };
      setTask(newTask);
      const newTaskList = taskList.concat(newTask);
      setTaskList(newTaskList);
    }
  };

  // Remove record from list
  function deleteTask(id) {
    const newTaskList = taskList
      .map((task) => {
        if (id === task.id) {
          return null;
        }
        return task;
      })
      .filter(Boolean); // filter(Boolean) will delete null from list
    setTaskList(newTaskList);
  }

  // CheckBox handler
  const updateCheckBox = (id, checkField) => {
    const newTaskList = taskList.map((record) => {
      if (id === record.id) {
        return { ...record, isDone: !checkField };
      }
      return record;
    });
    setTaskList(newTaskList);
  };

  // Clear All
  const clearHandle = () => {
    setTaskList([]);
    setTask({ id: 0, toDo: 'Empty', isDone: false });
  };

  // Refresh list
  const refreshHandle = () => {
    let newTaskList = [];
    let counter = 0;
    taskList.forEach((record) => {
      counter++;
      newTaskList.push({ ...record, id: counter });
    });
    setTaskList(newTaskList);
  };

  return (
    <div className='ToDoList'>
      {taskList.map((record) => {
        return (
          <ToDo
            key={record.id}
            handleCheck={updateCheckBox}
            deleteTask={deleteTask}
            {...record}
          />
        );
      })}
      <Adding addTask={addTask} />
      <RefreshClear clearHandle={clearHandle} refreshHandle={refreshHandle} />
    </div>
  );
}

export default ToDoList;
