import React from 'react';
import './RefreshClear.css';

// Refresh or clear list
function RefreshClear({ clearHandle, refreshHandle }) {
  const handleRefresh = () => {
    refreshHandle();
  };

  const handleClear = () => {
    clearHandle();
  };

  return (
    <div className='refreshClear'>
      <button className='refreshBtn btn' onClick={handleRefresh}>
        Refresh
      </button>
      <button className='clearBtn btn' onClick={handleClear}>
        Clear All
      </button>
    </div>
  );
}

export default RefreshClear;
